@import url(https://use.typekit.net/wxu0xmo.css);
.navbar {
  background-color: #F8F7F3;
  position: fixed;
  top:0;
  width: 100%;
  padding: 30px;
  box-sizing: border-box;
  padding-bottom: 10px;
  z-index: 500;
  height: 160px;
}

.navbar-search {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color:#D06426;
  height: 70px;
  padding-left: 20px;
  padding-right: 20px;
}

.navbar-logo {
  display: flex;
  align-items: center;
  white-space: nowrap;
  font-size: 25px;
  cursor: pointer;
}

.navbar-searchbar {
  height: 100%;
  display: flex;
  align-items: center;
  margin: 20px;
  flex-grow: 1;
}
.navbar-searchbar input {
  height: 20px;
  flex-grow: 1;
  min-width: 150px;
  font-size: 13px;
  font-weight: lighter;
  padding: 10px;
}

.search-icon {
  padding: 12px;
  cursor: pointer;
  -webkit-transition: all .5s ease;
  transition: all .5s ease;
}
.search-icon:hover {
  -webkit-transform: scale(1.1) rotate(20deg);
          transform: scale(1.1) rotate(20deg);
}

.navbar-basket {
  font-size: 20px;
  margin: 20px;
  cursor: pointer;
}

.navbar-navitems-list {
  margin: 0;
  padding: 20px;
}

.navbar-navitems {
  width: 800px;
}
.nav-list-item {
  display: inline;
  list-style: none;
  margin: 10px;
  cursor: pointer;
}

.nav-list-item:first-child {
  display: inline;
  list-style: none;
  margin-left: 0px;
  cursor: pointer;
}
.nav-list-item:hover{
  text-decoration: underline;
}

.drop-down-shop {
  margin-left: 5px;
  margin-right: 5px;
  font-size: 10px;
  top: -1px;
  position: relative;
}

.delivery-time {
  white-space: nowrap;
  margin: 0;
  font-size: 20px;
  cursor: default;
}

@media only screen and (max-width: 841px) {
  .delivery-time {
    display: none;
  }
  .sub-menus {
    display: none;
  }

  .navbar {
    background-color: #F8F7F3;
    padding: 10px;
    width: 100%;
    height: 140px;
  }
  .delivery-time {
    display: none;
  }

  .navbar-navitems{
    display: none;
  }

  .navbar-search {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    color:#D06426;
    height: 70px;
    padding-left: 20px;
    padding-right: 20px;
  }
  .navbar-searchbar {
    width: 100%;
    margin: 10px;
  }
  .navbar-basket {
    display: none;
  }

  .site-title{
    margin-top:10px;
    margin-bottom: 0px;
    cursor: default;
  }

  .sub-menus {
    display: none;
  }

}


.sub-menu-list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.sub-menu {
  list-style: none;
  padding: 0;
  display: inline-block;
  margin-left: 50px;
  vertical-align:top;
}

.sub-menu:first-child {
  margin-left: 0px;

}

.hamburger{
  display: none !important;
}

.sub-menu-list-item {
  margin-bottom:6px;
  font-size: 13px;
  cursor: pointer;
}

.sub-menus {
  width: 100vw;
  padding-bottom: 15px;
  background-color: #F8F7F3;
  position: fixed;
  z-index: 400;
  top: 0;
  -webkit-transform: translateY(160px);
          transform: translateY(160px);
  padding-top: 5px;
  padding-left: 50px;
  -webkit-transition: -webkit-transform 1s ease;
  transition: -webkit-transform 1s ease;
  transition: transform 1s ease;
  transition: transform 1s ease, -webkit-transform 1s ease;
}

.sub-menu-title {
  margin-top: 0;
  font-size: 17px;
  cursor: pointer;
  color:#D06426;
}

@media only screen and (max-width: 1040px) {
  .sub-menu-title {
    margin-top: 0;
    font-size: 10px;
  }
  .sub-menu-list-item {
    margin-bottom:6px;
    font-size: 10px;
  }
  .hamburger{
    display: inline !important;
    height: 5px;
    width: 10px;
    position: absolute;
    left: 0;
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
    color: #D06426 !important;
  }
}

.sub-menus.isHidden {
  -webkit-transform: translateY(-160px);
          transform: translateY(-160px);
}

@media only screen and (min-width: 842px) {
  .hamburger{
    display: none !important ;
  }
}
@media only screen and (max-width: 842px) {
  .sub-menus {
    -webkit-transform: translateY(-160px);
            transform: translateY(-160px);
  }
}

